@media (max-width: 425px) {


  .popUpMenuContainer{
    /* margin-top: 100px; */
    /* top:0; */
    position: absolute;
    /* margin:auto; */
    text-align: right;
    padding:24px;
    border-radius: 10px;
    right:16px;
    left:16px;
    bottom:85px;
  
  
    background: #F5F3ED;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    animation: showMobileMenu  0.7s;
    }
  
    @keyframes showMobileMenu {
      from {
        bottom:65px;
        padding-top:32px;
        padding-bottom:32px;
        opacity: 0%;
