      to {
        bottom: 85px;
        padding-top:24px;
        padding-bottom:24px;
        opacity: 100%;
      }
    }
  
    .downBtnMenu{
      position: absolute;
      text-align:center;
      padding-top:15px;
      padding-bottom:15px;
      bottom:16px;
      right:82px;
      left:16px;
      background: #F5F3ED;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
      border-radius: 50px;
      animation: showDownBtnMenu 0.7s;
    }
  
    @keyframes showDownBtnMenu {
      from {
        right:70px;
        left:32px;
        opacity: 0%;
      }
      to {
        right:82px;
        left:16px;
        opacity: 100%;
      }
    }


}

